@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600;700&display=swap);
.header {
    display: flex;
    justify-content: flex-start;
    height: 76px;
    border-bottom: 1px solid #1111111a;
    align-items: center;
}

.h_nav {
    display: flex;
    width: 100%;
    justify-content: center;
}

.h_logo {
    margin-left: 51px;
}

.h_link {
    font-size: 14px;
    line-height: 24px;
    margin: 0 12px;
    color: #1111117a;
    text-decoration: none;
    transition: all 0.3s;
    letter-spacing: 0px;
}

.h_link:hover {
    color: #111111cc;
}

@media screen and (max-width: 470px) {
    .h_logo {
        margin-left: 0;
    }
}

@media screen and (max-width: 390px) {
    .header_link {
        margin: 0 12px 0 0;
    }
}

.footer {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 76px;
    background: #FAFAFA;
    box-shadow: 0px 1px 0px rgba(17, 17, 17, 0.1);
}

.f_text {
    font-weight: 400;
    line-height: 16px;
    font-size: 13px;
    font-style: normal;
    letter-spacing: 0px;
}

.input_container {
    display: flex;
    flex-direction: column;
    position: relative;
    width: 100%;
    margin: 5px 0;
}
.u_input {
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    padding-left: 16px;
    padding-top: 20px;
    background: #FFFFFF;
    border: 1px solid #F1F1F1;
    border-radius: 4px;
    height: 56px;
    max-width: 616px;
    line-height: 24px;
    letter-spacing: 0px;
    transition: 0.3s;
}

.u_label {
    position: absolute;
    font-size: 13px;
    font-style: normal;
    left: 16px;
    top: 8px;
    font-weight: 400;
    line-height: 16px;
    letter-spacing: 0px;
    color: #1111117A;
}



.u_input:hover, .i_input:focus {
    border: 1px solid #6e6e6e !important;
}

.u_input::-webkit-outer-spin-button,
.u_input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

.u_input[type=number] {
    -moz-appearance: textfield;
}

.button {
    display: flex;
    text-align: left;
    align-items: center;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    color: #01a7fd;
    transition: 0.3s;
    line-height: 24px;
    letter-spacing: 0px;
    height: 44px;
    border: none;
    background: white;
}

.border {
    border: 2px solid #01a7fd;
    border-radius: 100px;
    padding: 10px 20px;
}

.border:hover {
    background-color: rgb(236, 251, 253);
    color: #01a7fd;
}

.button:hover {
    color: #df0000;
}

.blue {
    color: white;
    padding: 10px 20px;
    border-radius: 100px;
    background: #01a7fd;

}

.blue:hover {
    color: white;
    background: #df0000;
}

.children_form {
    display: grid;
    align-items: center;
    grid-gap: 18px;
    grid-template-columns: 1fr 1fr auto;
}

@media screen and (max-width: 529px) {
    .children_form {
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 0;
    }
}

.form_parent_data {
    display: flex;
    flex-direction: column;
}
.add_children_block {
    display: flex;
    align-items: center;
    margin-top: 18px;
    justify-content: space-between;
   }

.add_data_block {
    margin-top: 25px;
}

.plus_img {
    margin: 0 8px 0 4px;
}


@media screen and (max-width: 529px) {
    .add_data_block {
        display: flex;
        justify-content: center;
    }
}

@media screen and (max-width: 470px) {
    .add_children_button_name  {
        display: none;
    }
}

.parent_data_block {
    margin-top: 20px;
}

.parent_data {
    font-size: 16px;
    font-style: normal;
    font-weight: 700;
    margin-top: 20px;
    letter-spacing: 0px;
    line-height: 24px;
}

.children_data_block {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
}

.child_card {
    background: #f1f1f1;
    border-radius: 5px;
    padding: 10px 20px;
    height: 44px;
    margin: 10px;
    margin-right: auto;
}

.child_data {
    line-height: 24px;
    font-size: 16px;
    font-weight: 700;
    font-style: normal;
    letter-spacing: 0px;
}

*{
    font-family: 'Montserrat', sans-serif;
    box-sizing: border-box;
    margin: 0;
    padding: 0;


}
*.ui_input :active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}

.container {
    margin: 0 auto;
    width: 1366px;
    height: calc(100vh - 76px - 76px);
       padding: 30px 375px 30px 375px;

}

h2{
    font-weight: 500;
    font-size: 16px;
    font-style: normal;
    padding: 30px 0 10px;
    line-height: 24px;
    letter-spacing: 0px;
    }


@media screen and (max-width: 700px) {
    .container {
        width: auto;
        margin: 0 42px;
            }
}

