.children_form {
    display: grid;
    align-items: center;
    grid-gap: 18px;
    grid-template-columns: 1fr 1fr auto;
}

@media screen and (max-width: 529px) {
    .children_form {
        display: flex;
        flex-direction: column;
        align-items: center;
        grid-gap: 0;
    }
}
